"use client";

import React from "react";
import { SignedIn, SignedOut, SignInButton, SignUpButton, UserButton, useClerk} from "@clerk/nextjs";
import { BarLoader } from "react-spinners";
import { useStoreUser } from "../hooks/storeUserDB";
import { Unauthenticated } from "convex/react";

const Header = () => {
  const { isLoading } = useStoreUser();

  return (
    <header className="fixed top-0 w-full border-b bg-white/95 backdrop-blur z-50 supports-[backdrop-filter]:bg-white/60">
      <nav className="flex items-center justify-between px-6 py-3">
        <div className="text-xl font-semibold">Expense Splitter</div>
        <div>
          <SignedOut>
            <div className="flex gap-2">
              <SignInButton mode="redirect" redirecturl="/dashboard" />
              <SignUpButton mode="redirect" redirecturl="/dashboard" />
            </div>
          </SignedOut>
          <SignedIn>
            <UserButton afterSignOutUrl="/" />
          </SignedIn>
        </div>

        <div>
          <Unauthenticated>
            <SignInButton>
              <Button variant={"ghost"}>Sign In</Button>
            </SignInButton>

            <SignUpButton>
              <Button className="bg-green-600 hover:bg-green-700 border-none">
                Get Started
              </Button>
            </SignUpButton>
          </Unauthenticated>
        </div>

      </nav>

      {isLoading && <BarLoader width={"100%"} color="#36d7b7" />}
    </header>
  );
};

export default Header;
